/*------------------------------------*\
  #COLORS
\*------------------------------------*/

// basic colors

$black: #000000 !default;
$white: #ffffff !default;


// rainbow colors

$silver : #dddddd !default;
$gray   : #aaaaaa !default;
$navy   : #001f3f !default;
$blue   : #0074d9 !default;
$aqua   : #7fdbff !default;
$teal   : #39cccc !default;
$olive  : #3d9970 !default;
$green  : #2ecc40 !default;
$lime   : #01ff70 !default;
$yellow : #ffdc00 !default;
$orange : #ff851b !default;
$red    : #ff4136 !default;
$maroon : #85144b !default;
$fuchsia: #f012be !default;
$purple : #b10dc9 !default;


// RGBA colors

$transparent: rgba(0, 0, 0, 0) !default;
$full-black : rgba(0, 0, 0, 1) !default;
$dark-black : rgba(0, 0, 0, 0.87) !default;
$light-black: rgba(0, 0, 0, 0.54) !default;
$min-black  : rgba(0, 0, 0, 0.26) !default;
$faint-black: rgba(0, 0, 0, 0.12) !default;
$full-white : rgba(255, 255, 255, 1) !default;
$dark-white : rgba(255, 255, 255, 0.87) !default;
$light-white: rgba(255, 255, 255, 0.54) !default;
$light-grey : rgba(239, 239, 239, 1) !default;

$darken-1: rgba(0, 0, 0, 0.0625) !default;
$darken-2: rgba(0, 0, 0, 0.125) !default;
$darken-3: rgba(0, 0, 0, 0.25) !default;
$darken-4: rgba(0, 0, 0, 0.5) !default;

$lighten-1: rgba(255, 255, 255, 0.0625) !default;
$lighten-2: rgba(255, 255, 255, 0.125) !default;
$lighten-3: rgba(255, 255, 255, 0.25) !default;
$lighten-4: rgba(255, 255, 255, 0.5) !default;





// tell the rest of framework that we have this module

$wocss-settings-colors: true;



